import posed from 'react-pose'

const SubNav = posed.nav({
	  open: { x: '0%', staggerChildren: 100  },
	  closed: { x: '-100%'  }
})

class Nav extends React.Component {

	constructor(props) {
		super(props)

		this.handleClick = this.handleClick.bind(this)
	}

	componentDidMount() {
		this.scrollTo = require('react-scroll-to-component')
	}

	handleClick(link) {
		this.props.changeSection(link.href)
	}

	render () {
		return (
			<div className='nav-container'>
				<nav className={this.props.isVisible ? "main-nav" : "main-nav main-nav--fixed"}>
						<div className='wrap'>
							{this.props.links.map((link, i) => {
								return (
									<a className={this.props.navActiveItem === link.href ? "main-nav__link active" : "main-nav__link"}
									   key={i}
									   href={link.href}
										onClick={(event) => {
											event.preventDefault()
											this.handleClick(link)
										}}>
									   {link.name}
									</a>
								)
							})}
						</div>
				</nav>
			</div>
		)
	}

}

export default Nav
